'use client'

import React, { useState, useEffect } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { useSocket } from '../../custom-hooks/useSocket'

const SlotGame = () => {
    const userId = 'user_123'
    const { gameState, spinResult, isConnected, spin } = useSocket(userId)

    const [grid, setGrid] = useState<string[][]>([
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
    ])
    const [spinning, setSpinning] = useState<boolean>(false)
    const [balance, setBalance] = useState<number>(1000)
    const [betAmount, setBetAmount] = useState<number>(10)
    const [lastWinnings, setLastWinnings] = useState<number>(0)
    const [activeColumns, setActiveColumns] = useState<number>(-1)
    const [spinningGridData, setSpinningGridData] = useState<string[][]>([])

    const symbolOptions = ['ðŸ’', 'ðŸ‡', 'ðŸŠ', 'ðŸ’Ž', 'ðŸ€', 'ðŸ‹']

    // Helper to generate a grid of random symbols
    const generateRandomGrid = () => {
        return Array.from({ length: 3 }, () =>
            Array.from(
                { length: 5 },
                () =>
                    symbolOptions[
                        Math.floor(Math.random() * symbolOptions.length)
                    ]
            )
        )
    }

    // Start spinning animation
    useEffect(() => {
        if (spinning) {
            setSpinningGridData(generateRandomGrid()) // Initialize spinning grid
            let column = 0
            console.log('salam')
            const interval = setInterval(() => {
                if (column < grid[0].length) {
                    setActiveColumns(column)
                    column++
                } else {
                    clearInterval(interval)
                    setSpinning(false)
                    setGrid(spinResult.grid) // Final grid from server
                }
            }, 500) // 500ms delay between column stops
        }
    }, [spinning])

    const renderSymbol = (symbol: string) => {
        const symbolMap: { [key: string]: string } = {
            'ðŸ’': 'ðŸ’',
            'ðŸ‹': 'ðŸ‹',
            'ðŸ‡': 'ðŸ‡',
            'ðŸŠ': 'ðŸŠ',
            'ðŸ’Ž': 'ðŸ’Ž',
            'ðŸ€': 'ðŸ€',
            '': 'â“',
        }
        return symbolMap[symbol]
    }

    const handleSpin = () => {
        if (isConnected && gameState) {
            if (betAmount < gameState.minBet || betAmount > gameState.maxBet) {
                alert(
                    `Bet must be between ${gameState.minBet} and ${gameState.maxBet}`
                )
                return
            }
            setSpinning(true)
            spin(betAmount)
        }
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-[#1a1a2e] via-[#16213e] to-[#0f3460] flex items-center justify-center p-4">
            <Card className="w-full max-w-4xl bg-[#0f3460] border-4 border-[#e94560] shadow-2xl">
                <CardContent className="p-6">
                    <div className="grid grid-cols-5 gap-4 mb-6">
                        {spinning
                            ? spinningGridData.map((row, rowIndex) =>
                                  row.map((symbol, colIndex) => (
                                      <div
                                          key={`${rowIndex}-${colIndex}`}
                                          className={`bg-[#1a1a2e] border-2 border-[#e94560] rounded-lg h-24 flex items-center justify-center text-white text-4xl font-bold ${
                                              colIndex <= activeColumns
                                                  ? 'opacity-100'
                                                  : 'opacity-50'
                                          }`}
                                      >
                                          {renderSymbol(symbol)}
                                      </div>
                                  ))
                              )
                            : grid.map((row, rowIndex) =>
                                  row.map((cell, colIndex) => (
                                      <div
                                          key={`${rowIndex}-${colIndex}`}
                                          className="bg-[#1a1a2e] border-2 border-[#e94560] rounded-lg h-24 flex items-center justify-center text-white text-4xl font-bold"
                                      >
                                          {renderSymbol(cell)}
                                      </div>
                                  ))
                              )}
                    </div>

                    <div className="flex justify-between items-center">
                        <div className="flex space-x-4 items-center">
                            <Button
                                variant="destructive"
                                className="bg-[#e94560] hover:bg-[#ff6b81] text-white font-bold py-3 px-6 rounded-lg"
                                onClick={handleSpin}
                                disabled={spinning}
                            >
                                SPIN
                            </Button>
                            <div className="flex items-center space-x-2">
                                <span className="text-white">Bet:</span>
                                <input
                                    type="number"
                                    value={betAmount}
                                    onChange={(e) =>
                                        setBetAmount(Number(e.target.value))
                                    }
                                    min="1"
                                    max={balance}
                                    className="w-20 p-2 rounded bg-[#1a1a2e] text-white"
                                />
                            </div>
                        </div>

                        <div className="text-white text-xl flex items-center space-x-4">
                            <div>
                                <span>Balance: </span>
                                <span className="font-bold text-[#e94560]">
                                    ${balance.toFixed(2)}
                                </span>
                            </div>
                            <div>
                                <span>Last Winnings: </span>
                                <span className="font-bold text-green-500">
                                    ${lastWinnings.toFixed(2)}
                                </span>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}

export default SlotGame
